.hub-footer {
  background-color: #F6F4F1;
  padding: 2rem 2rem 1rem 2rem;

  @media screen and (min-width: 990px) {
    padding: 4rem 4rem 2rem 4rem;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #000;
    padding-top: 2rem;
    gap: 4rem;

    @media screen and (min-width: 990px) {
      padding-top: 4rem;
    }
    @media screen and (max-width: 500px) {
      gap: 2rem;
    }
  }

  &__body {
    display: flex;
    align-items: flex-start;
    gap: 2rem;

    @media screen and (max-width: 600px) {
      flex-direction: column;
    }
  }

  &__heading {
    width: 60%;
    &--content {
      font-family: var(--heading-font-big), sans-serif;
      margin: 0;
    }

    @media screen and (max-width: 600px) {
      width: 100%;
    }
  }

  &__media-wrapper {
    position: relative;
    height: 0;
    width: 40%;
    padding-bottom: 50%;
    animation: skeleton-loading 1s linear infinite alternate;

    @media screen and (max-width: 600px) {
      width: 85%;
      padding-bottom: calc(125% / 100 * 85);
    }

    &:has(.placeholder) {
      animation: none;
      background-color: rgb(200, 200, 200);
    }
  }

  &__media-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__logo {
    margin-block: 1rem 0;
    letter-spacing: clamp(2px, .5vw, 5px);
  }

  &__collection-title {
    margin: 0;
  }
}