.hub-cards {
  padding: 2rem;

  &--wrapper {
    overflow: hidden;
  }

  @media screen and (min-width: 990px) {
    padding: 4rem;
  }
  &__slide {
    &--wrapper {
      display: block;
      position: relative;
      height: 0;
      padding-bottom: 120%;
      overflow: hidden;
    }
  }

  &__media-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    display: block;
    margin-top: 1rem;
    font-size: 14px;
    font-family: var(--heading-font-small);
  }

  &__cta {
    display: block;
    margin-top: 1rem;
    text-transform: uppercase;

    span {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 100px;
      color: #fff;
      background-color: #000;
      padding: .5rem 1rem;
      font-size: 12px;
    }
  }
}