.hub-features {
  padding: 2rem;

  @media screen and (min-width: 990px) {
    padding: 4rem;
  }

  &__body {
    position: relative;
    contain: layout;
  }

  &__item {
    padding-block: 4rem;    
  }

  &__media-content {
    display: flex;
    align-items: center;
    justify-content: center;

    &[data-media-count="3"] {
      @media screen and (max-width: 600px) {
        flex-direction: column;
      }
    }
    &[data-media-count="2"] {
      @media screen and (max-width: 600px) {
        flex-direction: column-reverse;
      }
    }
  }

  &__media-content[data-media-count="3"] ~ &__description {
    @media screen and (max-width: 600px) {
      margin: 1rem 0 0 0;
    }
  }

  &__media-wrapper {
    position: relative;
    animation: skeleton-loading 1s linear infinite alternate;

    &:has(.placeholder) {
      animation: none;
      background-color: rgb(200, 200, 200);
    }

    &--wide {
      width: 55%;
      padding-bottom: 33%;
      z-index: 1;
      transform: translateX(10%);

      @media screen and (min-width: 990px) {
        width: 50%;
        padding-bottom: 30%;
      }

      @media screen and (max-width: 769px) {
        width: 65%;
        padding-bottom: 39%;
      }

      @media screen and (max-width:600px) {
        transform: translateY(-15%);
        width: 100%;
        padding-bottom: 60%;
      }
    }

    &--medium {
      transition: .3s;

      &:first-child {
        width: 30%;
        padding-bottom: 41%;
        transform:translateX(15%);
        @media screen and (min-width: 990px) {
          width: 25%;
          padding-bottom: 30%;
          transform:translateX(15%);
        }
        @media screen and (max-width:600px) {
          transform: none;
          margin-right: auto;
          margin-bottom: -25%;
          width: 50%;
          padding-bottom: 65%;
        }
      }
      
      &:nth-child(2) {
        width: 40%;
        padding-bottom: 54%;
        z-index: 1;

        @media screen and (min-width: 990px) {
          width: 35%;
          padding-bottom: 42%;
          z-index: 1;
        }

        @media screen and (max-width:600px) {
          transform: none;
          margin: auto;
          width: 65%;
          padding-bottom: 80%;
        }
      }

      &:last-child {
        width: 30%;
        padding-bottom: 41%;
        transform:translateX(-15%);

        @media screen and (min-width: 990px) {
          width: 25%;
          padding-bottom: 30%;
          transform:translateX(-15%);
        }

        @media screen and (max-width:600px) {
          transform: none;
          margin-left: auto;
          margin-top: -25%;
          width: 50%;
          padding-bottom: 65%;
        }
      }
    }

    &--tall {
      width: 35%;
      padding-bottom: 49%;
      transform: translateX(-10%);

      @media screen and (min-width: 990px) {
        width: 30%;
        padding-bottom: 42%;
      }

      @media screen and (max-width: 769px) {
        width: 45%;
        padding-bottom: 63%;
      }

      @media screen and (max-width:600px) {
        transform: none;
        width: 65%;
        padding-bottom: 91%;
      }
    }
  }

  &__media-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    font-size: clamp(3rem, 4vw, 6rem);
    max-width: 150px;
    opacity: 0;
    transition: opacity 2s ease;
    @media screen and (min-width: 1025px) {
      max-width: 300px;
      margin: 0 0 -4rem 0;
    }
    @media screen and (max-width: 769px) {
      margin-bottom: 0;
    }
    @media screen and (max-width: 600px) {
      margin-bottom: 2rem;
    }
  }

  &__description {
    max-width: 250px;
    margin: -4rem 0 0 0;
    opacity: 0;
    transition: opacity 2s ease;

    @media screen and (max-width: 600px) {
      margin: -1rem 0 0 0;
    }
  }

  .swiper-slide-active &__title,
  .swiper-slide-active &__description {
    opacity: 1;
  }

  &__navigation {
    &--prev, &--next {
      z-index: 9;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background-color: #000;
      padding: 1rem;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      transform: translate(0, -50%)
    }

    &--prev {
      left: 0;
    }
    &--next {
      right: 0;
    }
  }

  &__footer {
    overflow: hidden;
  }

  &__footer:not(.swiper-container-initialized) &__footer-slider {
    gap: 1rem;
  }
  .swiper-non-slider &__footer-slider {
    column-gap: 1rem!important;
  }
  .swiper-non-slider[data-slider-type="product"] &__footer-slider {
    row-gap: 0!important;
  }

  &__footer-item {
    position: relative;
    width: 20%;
    &--feature {
      background-color: #D4D0CA;
      border-radius: 8px;
    }

    &--outer {
      padding-bottom: 90%;
      height: 0;
    }

    &--inner {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      padding: 1.5rem;
    }
  }

  &__footer-title {
    font-family: var(--heading-font-small);
    margin: 0;
  }

  &__icon {
    max-width: 40%;
    height: auto;
    margin: auto 0 0 auto;
    object-fit: contain;

    @media screen and (max-width: 450px) {
      max-width: 50%;
    }
    @media screen and (min-width: 990px) {
      max-width: 50%;
    }
  }
  .scroll-down__btn {
    position: sticky;
    bottom: 20px;
    width: max-content;
    margin: 0 auto 20px;
    z-index: 5;
    transition: transform 0.3s ease;
    animation: bounce 2s infinite;
    svg {
      width: 20px;
      height: 20px;
    }
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  60% {
    transform: translateY(-2px);
  }
}