.hub-models {
  position: relative;

  &__background {
    overflow-x: hidden;
    position: relative;
  }

  &__main {
    overflow-x: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__background-wrapper {
    position: relative;
    width: 100vw;
    height: 100vh; // fallback for browsers that do not support DVH
    height: 100dvh;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__navigation {
    position: absolute;
    top: 50%;
    z-index: 9;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    padding: 1rem;
    border-radius: 50%;


    &--prev {
      left: 2rem;

      @media screen and (min-width: 990px) {
        left: 4rem;
      }
    }

    &--next {
      right: 2rem;

      @media screen and (min-width: 990px) {
        right: 4rem;
      }
    }
  }

  &__model-item {
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
    transition: transform 1s;

    @media screen and (min-width: 800px) {
      max-height: 80%;
    }
  }

  &__main-item {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 100%;
    overflow: hidden;

    &.swiper-slide-active {
      img {
        transform: translateY(max(4vh, 10%)) scale(2.5); 
        @media screen and (min-width: 1140px) {
          transform: translateY(max(20vh, 30%)) scale(2); 
          @media screen and (min-height: 900px) {
            transform: translateY(max(5vh, 10%)) scale(2); 
          }
        }

        @media screen and (min-width: 1400px) {
          @media screen and (min-height: 900px) {
            transform: translateY(max(20vh, 25%)) scale(2); 
          }
        }

        @media screen and (max-height: 800px) {
          transform: translateY(max(20vh, 40%)) scale(2.5); 
        }

        @media screen and (max-width: 600px) {
          transform: translateY(max(10vh, 25%)) scale(2.2); 
          @media screen and (max-height: 750px) {
            transform: translateY(max(10vh, 40%)) scale(2.2); 
          }
        }

        @media screen and (max-width: 350px) {
          @media screen and (max-height: 500px) {
            transform: translateY(max(10vh, 45%)) scale(2); 
          }
        }
      }  
    }
  }

  &__title-container {
    overflow: hidden;
    position: absolute;
    bottom: 2rem;
    left: 2rem;
    width: 200px;
    background-color: #000;
    z-index: 9;
    padding: .5rem 0;
    border-radius: 100px;

    @media screen and (min-width: 990px) {
      bottom: 4rem;
      left: 4rem;
    }
  }

  &__title {
    display: flex;
    justify-content: center;
    span {
      display: inline-block;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
  }

  &__title-list {
    &--wrapper {
      align-items: center;
    }
  }
}