.hub-header {
  display: flex;
  padding: 2rem;
  gap: 1rem;

  @media screen and (min-width: 990px) {
    padding: 4rem;
    gap: 2rem;
  }

  @media screen and (max-width:600px) {
    display: grid;
  }

  &__left {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @media screen and (max-width:600px) {
      display: contents;
    }
  }

  &__right {
    width: 40%;
    @media screen and (max-width: 600px) {
      grid-row: 2/3;
      width: 100%;
      max-width: 85%;
      margin-left: auto;
    }
  }

  &__media-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 0;
    animation: skeleton-loading 1s linear infinite alternate;

    &--wide {
      padding-bottom: 65%;
      @media screen and (max-width: 600px) {
        max-width: 85%;
        padding-bottom: calc(65% / 100 * 85);
      }
    }
    &--tall {
      padding-bottom: 135%;
    }
    &:has(.placeholder) {
      animation: none;
      background-color: rgb(200, 200, 200);
    }
  }

  &__media-item {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__heading {
    margin-block: 3rem 1rem;
    width: 80%;
    text-transform: uppercase;

    @media screen and (max-width: 600px) {
      margin-block: 1rem 0 ;
    }

    &--content {
      margin: 0;
    }
  }

  &__label {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #000;
    color: #fff;
    padding: .3rem .8rem;
    border-radius: 40px;
    font-family: var(--heading-font-small), sans-serif;
    margin-top: auto;

    @media screen and (min-width: 990px) {
      padding: .8rem 1.6rem;
    }

    @media screen and (max-width: 600px) {
      justify-self: start;
    }

    .text {
      text-transform: uppercase!important;
      @media screen and (max-width: 990px) {
        font-size: 1rem!important;
      }
    }
  }

  &__media-control {
    z-index: 1;
    display: flex;
    position: absolute;
    bottom: 5%;
    left: 10%;
    width: 85%;
    gap: 1rem;

    @media screen and (max-width: 600px) {
        gap: 0;
        left: 2rem;
        bottom: 5%;
        width: calc(100% - 4rem)
    }
  }

  [data-state="playing"] {
    .Icon--play-outline {
      display: none;
    }

    .Icon--pause-outline {
      display: block;
    }
  }

  [data-state="paused"] {
    .Icon--play-outline {
      display: block;
    }

    .Icon--pause-outline {
      display: none;
    }
  }

  [data-state="muted"] {
    .Icon--unmute-outline {
      display: block;
    }

    .Icon--mute-outline {
      display: none;
    }
  }

  [data-state="unmuted"] {
    .Icon--unmute-outline {
      display: none;
    }

    .Icon--mute-outline {
      display: block;
    }
  }

  .Icon {
    width: 25px;
    height: 25px;
  }
}