.hub-text {
  padding: 2rem;
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 2rem;
  @media screen and (min-width: 990px) {
    padding: 4rem;
    gap: 4rem;
  }

  @media screen and (max-width: 769px) {
    grid-template-columns: 1fr;
  }

  &--small {
    font-size: clamp(14px, 1.5vw, 18px)
  }

  h2, p {
    margin: 0;
  }
}