.hub-slideshow {
  display: flex;
  flex-direction: column;
  padding: 8rem 0 2rem 2rem;
  @media screen and (min-width: 990px) {
    padding: 8rem 0 4rem 4rem;
    gap: clamp(2.5rem, 5vw, 7.5rem);
  }

  &__body {
    overflow-x: hidden;
    &--wrapper {
      gap: 2rem;
    }

    &.swiper-container-initialized &--wrapper {
      gap: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    align-items: flex-start;
    width: 30%;
    flex-shrink: 0;
    cursor: pointer;

    @media screen and (min-width: 990px) {
      gap: 1rem;
    }
  }

  &__item.grayscale:not(.swiper-slide-active):not(:hover) &__media-item,
  .swiper-untouched .grayscale:not(:hover) &__media-item {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
  }

  &__media-item {
    max-width: 100%;
    height: auto;
  }

  &__title {
    &--small {
      font-family: var(--heading-font-small);
      margin: 0;
      font-size: 18px;

      @media screen and (max-width: 600px) {
        font-size: 16px;
      }
    }
    &--big {
      font-family: var(--heading-font-big);
      font-size: clamp(3rem, 5vw, 10rem);
      margin: 0;
    }
  }

  &__description {
    margin-top: 0;
    width: 80%;
    font-size: clamp(16px, 2vw, 40px);

    @media screen and (max-width: 600px) {
      width: 100%;
    }
  }

  &__cta {
    display: inline-block;
    background-color: #000;
    color: #fff!important;
    font-family: var(--heading-font-small);
    padding: .5rem 1.5rem;
    border-radius: 20px;
    text-transform: uppercase;
    font-size: 12px;

    @media screen and (max-width: 600px) {
      padding: .3rem 1.2rem;
    }

    &--primary {
      padding: 1rem 2.5rem;
      font-size: 12px;
    }
  }

  &__footer {
    overflow-x: hidden;
    margin-right: 2rem;
    padding-top: 7rem;

    @media screen and (min-width: 990px) {
      margin-right: 4rem;
      padding-top: clamp(2.5rem, 5vw, 7.5rem);
    }
  }

  &__footer &__navigation {
    @media screen and (min-width: 1025px) {
      display: none;
    }
  }

  &__item-details {
    display: flex;
    flex-shrink: 0;
    justify-content: space-between;
    gap: 5rem;

    @media screen and (max-width: 1025px) {
      gap: 2rem;
    }

    @media screen and (max-width: 990px) {
      flex-direction: column;
      gap: 3rem;
    }

    &--wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 3rem;
      width: 60%;
      flex-shrink: 0;

      @media screen and (max-width: 990px) {
        width: 100%;
        gap: 0;
      }
    }
    &:not(.swiper-slide-active) &--wrapper {
      opacity: 0;
    }
  }

  .swiper:not(.swiper-container-initialized) &__item-details {
    width: calc(100% - 4rem)!important;
    
    @media screen and (max-width: 990px) {
      width: calc(100% - 2rem)!important;
    }
  }

  &__item-details &__media-item {
    max-height: 90vh;
    object-fit: cover;
  }

  &__navigation {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;

    &--prev, &--next {
      --fill: #fff;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background-color: #000;
      padding: 1rem;
      border-radius: 50%;

      @media screen and (hover: hover) {
        &:hover {
          --fill: #000;
          background: #fff;
          box-shadow: 0 0 0 1px #000 inset;
        }
      }

      &:active {
        --fill: #000;
        background: var(--silver);
        box-shadow: 0 0 0 2px #000 inset;
      }
    
      svg {
        width: clamp(15px, 1vw, 20px);
        height: clamp(15px, 1vw, 20px);
      }
    }
  }

  &__features {
    display: inline-grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
    max-width: 100%;

    @media screen and (min-width: 990px) {
      max-width: calc(100% / 3 * 2);
    }

    ul {
      padding-left: 20px;
    }
  }

  @media screen and (max-width:1025px) {
    .tablet-hidden {
      display: none;
    }
  }
}