.hub-video {
  position: relative;
  &__media-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: var(--aspect-ratio-desktop);

    @media screen and (max-width: 1024px) {
      padding-bottom: var(--aspect-ratio-tablet);
    }
    @media screen and (max-width: 769px) {
      padding-bottom: var(--aspect-ratio-mobile);
    }

    &[data-playing="false"] {
      .Icon--play {
        display: block;
      }
    }
  
    &[data-playing="true"] {
      .Icon--pause {
        display: block;
      }
    }
  
    &[data-volume="false"] {
      .Icon--volume-off-crop {
        display: block;
      }
    }
  
    &[data-volume="true"] {
      .Icon--volume-on {
        display: block;
      }
    }
  }

  &__media-item {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    z-index: 2;

    &--poster {
      z-index: 1;
    }
  }

  &__media-control {
    position: absolute;
    bottom: 2rem;
    cursor: pointer;
    z-index: 3;

    &--video {
      right: 2rem;
    }
    &--audio {
      left: 2rem;
    }

    @media screen and (min-width: 990px) {
      bottom: 4rem;
      &--video {
        right: 4rem;
      }
      &--audio {
        left: 4rem;
      }
    }

    .Icon {
      display: none;
      width: clamp(35px, 3vw, 50px);
      height: clamp(35px, 3vw, 50px);
      background: rgba(255, 255, 255, .38);
      border-radius: 50%;
      padding: clamp(.5rem, 1vw, 1rem);
    }
  }
}