.hub-main {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 2rem;
  gap: clamp(4rem, 5vw, 10rem);
  background-color: #F6F4F1;
  @media screen and (max-width: 600px) {
    flex-direction: column;
  }

  @media screen and (min-width: 990px) {
    padding: 4rem;
  }

  &__left, &__right {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: clamp(2rem, 5vw, 10rem);

    @media screen and (max-width: 600px) {
      display: contents;
    }
  }

  &__item {
    --alignment-offset: clamp(1rem, 7vw - 1.5rem, 16rem);
    width: calc(100% - var(--alignment-offset));

    @media screen and (max-width: 600px) {
      width: 85%;
    }

    &:has([data-ratio="ratio-4"]) {
      width: 100%;
    }
    
    &[data-alignment="right"] {
      margin-left: auto;
      margin-right: 0;
    }

    &[data-alignment="left"] {
      margin-right: auto;
      margin-left: 0;
    }

    @media screen and (max-width:600px) {
      &[data-alignment-mob="right"] {
        margin-left: auto;
        margin-right: 0;
      }
  
      &[data-alignment-mob="left"] {
        margin-right: auto;
        margin-left: 0;
      }
    }

    p {
      font-family: var(--heading-font-small);
      margin: .5rem 0 0 0;

      @media screen and (min-width: 990px) {
        margin: 1rem 0 0 0;
      }
    }
    @media screen and (min-width:990px) {
      @media screen and (hover: hover) {
        &.show-on-hover:not(:hover) {
          .hub-main__overlay,
          .hub-main__content {
            display: none;
          }
        }
      }
    }
  }

  &__media-wrapper {
    position: relative;
    overflow: hidden;
    height: 0;
    animation: skeleton-loading 1s linear infinite alternate;

    &[data-ratio="ratio-1"] {
      padding-bottom: 145%;
    }
    &[data-ratio="ratio-2"] {
      padding-bottom: 110%;
    }
    &[data-ratio="ratio-3"] {
      padding-bottom: 120%;
    }
    &[data-ratio="ratio-4"] {
      padding-bottom: 75%;
    }

    &:has(.placeholder) {
      animation: none;
      background-color: rgb(200, 200, 200);
    }
  }

  &__content {
    position: absolute;
    top: 10%;
    left: 10%;
    width: 80%;
    height: 80%;
    z-index: 3;
    @media screen and (max-width: 600px) {
      top: 5%;
      left: 5%;
      width: 90%;
      height: 90%;
    }

    &--wrapper {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      color: #fff;

      & > * {
        margin: 0!important;
      }
    }
  }

  &__text {
    font-size: 1.4rem;
  }

  &__cta {
    color: #fff!important;
  }

  &__overlay {
    background: linear-gradient( rgba(0,0,0,0), 60%,  var(--overlay-opacity));
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}

  &__media-item {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    z-index: 1;
  }

  &__overlays {
    &--wrapper {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 999999999;
      overflow-y: auto;
      overflow-x: hidden;
      background: rgba(246, 244, 241, 0.9);
      backdrop-filter: blur(30px);
      -webkit-backdrop-filter: blur(30px);
      visibility: hidden;
      opacity: 0;
      transition: all .5s;
    }

    &.active &--wrapper, 
    &.active &--list, 
    &.active &--close {
      visibility: visible;
      opacity: 1;
    }

    & [data-trigger-id] {
      cursor: default;
    }
  }

  &__overlays.active &__overlay-item.active {
    opacity: 1;
    visibility: visible;
    height: 100%;
    overflow: visible;
  }

  &__overlay-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: opacity .5s;
  }

  &__overlays--close {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 2rem;
    right: 2rem;
    z-index: 1;
    background: #000;
    border-radius: 20px;
    padding: .5rem 1.5rem;
    z-index: 9999999999;
    visibility: hidden;
    opacity: 0;
    transition: all .5s;

    @media screen and (min-width: 990px) {
      padding: .5rem 2.5rem;
      right: 4rem;
    }

    svg {
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%)
    }

    span {
      width: 15px;
      color: transparent;
      font-family: var(--heading-font-small);
      font-size: 14px;
    }
  }

  &__overlays--list {
    display: flex;
    position: fixed;
    left: 2rem;
    top: 2rem;
    z-index: 1;
    padding: .5rem 3rem;
    background: #000;
    gap: 2rem;
    border-radius: 20px;
    z-index: 9999999999;
    max-width: min(65vw, 300px);
    overflow-x: hidden;
    visibility: hidden;
    opacity: 0;
    transition: all .5s;
    font-size: 14px;

    @media screen and (min-width: 990px) {
      left: 4rem;
      max-width: min(65vw, 450px);
    }

    &::-webkit-scrollbar {
      display: none;
    }

    .swiper-slide {
      color: #595959;
      cursor: pointer;
      text-transform: uppercase;
      font-family: var(--heading-font-small);
      white-space: nowrap;
      width: auto;
      &.active {
        color: #fff;
      }
      @media screen and (hover: hover) {
        &:hover {
          color: #fff;
        }
      }
    }
  }
}