@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.hub-fit {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  padding: 8rem 2rem 0;
  gap: 1rem;

  @media screen and (min-width: 990px) {
    padding: 8rem 4rem 0;
    gap: 0;
  }

  &__media-item {
    position: relative;
    max-height: clamp(65vh, 65vw, calc(100vh - 8rem));
    width: auto;
    object-fit: contain;
    display: block;
    margin: auto;
  }

  &__media-container {
    width: 100%;
    border-bottom: 1px solid #000;
    display: flex;
    justify-content: center;

    @media screen and (min-width: 1025px) {
      border:none;
    }
  }

  &__media-wrapper {
    position: relative;
  }

  &__item {
    position: absolute;
    z-index: 1;
    padding-inline: .5rem;
    top: var(--vertical-mobile);
    max-width: 30vw;
    font-size: 12px;
    display: none;
    &.active {
      display: block;
    }

    @media screen and (min-width: 769px) {
      font-size: 12px;
      display: block;
    }

    @media screen and (max-width: 600px) {
      max-width: 20vw;
    }
    @media screen and (max-width: 400px) {
      font-size: 11px !important;
      margin: 0 4px 0;
      padding: 0;
      max-width: 12vw;
    }

    &--left {
      left: var(--horizontal-mobile);
      transform: translate(-100%);
    }
    &--right {
      right: var(--horizontal-mobile);
      transform: translate(100%);
    }

    &--pointer {
      color: transparent;
      position: absolute;
      top: var(--vertical-mobile);
      width: var(--width-mobile);

      @media screen and (max-width: 769px) {
        display: none;
      }

      &--left {
        left: var(--horizontal-mobile);
      }
      &--right {
        right: var(--horizontal-mobile);
      }

      &::after {
        content: "";
        position: absolute;
        background: #000;
        height: 2px;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50px;
      }
    }

    @media screen and (min-width: 769px) {
      top: var(--vertical-tablet);
      &--left {
        left: var(--horizontal-tablet);
        transform: translate(-100%);
      }
      &--right {
        right: var(--horizontal-tablet);
        transform: translate(100%);
      }

      &--pointer {
        color: transparent;
        position: absolute;
        top: var(--vertical-tablet);
        width: var(--width-tablet);
  
        &--left {
          left: var(--horizontal-tablet);
        }
        &--right {
          right: var(--horizontal-tablet);
        }
  
        &::after {
          content: "";
          position: absolute;
          background: #000;
          height: 2px;
          width: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    @media screen and (min-width: 1139px) {
      top: var(--vertical-desktop);
      &--left {
        left: var(--horizontal-desktop);
        transform: translate(-100%);
      }
      &--right {
        right: var(--horizontal-desktop);
        transform: translate(100%);
      }

      &--pointer {
        color: transparent;
        position: absolute;
        top: var(--vertical-desktop);
        width: var(--width-desktop);
  
        &--left {
          left: var(--horizontal-desktop);
        }
        &--right {
          right: var(--horizontal-desktop);
        }
  
        &::after {
          content: "";
          position: absolute;
          background: #000;
          height: 1px;
          width: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }

  &__title {
    margin: 0;
    font-size: clamp(2.5rem, 5vw, 6rem);
    text-align: start;
    width: 100%;
    @media screen and (min-width: 990px) {
      position: absolute;
      left: 4rem;
      top:8rem;
      max-width: 20%;
    }
    @media screen and (min-width: 1520px) {
      max-width: 35%;
    }
  }
  &__description {
    margin: 2rem auto 2rem 0;
    text-align: start;
    max-width: 50%;


    @media screen and (min-width: 1025px) {
      position: absolute;
      left: 4rem;
      bottom: 4rem;
      max-width: 25%;
      margin: 0;
    }

    @media screen and (max-width: 600px) {
      max-width: 80%;
    }
  }

  .fit-viewer__hotspot {
    position: absolute;
    z-index: 30;
    top: var(--hotspot-vertical);
    &.hotspot--left {
      left: var(--hotspot-horizontal);
    }
    &.hotspot--right {
      right: var(--hotspot-horizontal);
    }
    &-inner {
      position: relative;
      width: 22px;
      height: 22px;
      border-radius: 50%;
      background-color: var(--hotspot-outline);
      mix-blend-mode: multiply;
      animation: pulse 1.25s linear infinite;
      cursor: pointer;
      transition: all 0.3s;
      opacity: 1;
      &:after {
        content: "";
        width: 8px;
        height: 8px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        mix-blend-mode: normal;
        box-shadow: 0px 0px 4px 2px rgba(0, 40, 85, 0.25);
        background: white;
        background-color: var(--hotspot-inner);
      }
    }
    &.active .fit-viewer__hotspot-inner {
      background-color: var(--hotspot-outline-active) !important;
    }
  }
}